import React from 'react';
import './cardStyle.css'
//üåëüçûüêë
const handleClick = (e) => {
    
    // console.log(e)
    // console.log(e.target.parentElement.innerText)
    // console.log(e.target.parentElement.count)

    var element2 = document.getElementById(e.target.parentElement.id);
    console.log(element2.getElementsByTagName('h2')[1].innerText)
    var count = Number(element2.getElementsByTagName('h2')[1].innerText)
    element2.getElementsByTagName('h2')[1].innerText = count + 1
    // element2.innerText = "YUP!"
}

const handleClickMinus = (e) => {
    var element2 = document.getElementById(e.target.parentElement.id);
    console.log(element2.getElementsByTagName('h2')[1].innerText)
    var count = Number(element2.getElementsByTagName('h2')[1].innerText)
    element2.getElementsByTagName('h2')[1].innerText = count - 1
}
const changeColor = (e) => {
    console.log(e.target.value)
    
    if (e.target.value == "none") {
        e.target.parentElement.style.background  = "grey"
      } else {
        e.target.parentElement.style.background  = e.target.value;
      }
}

export const Card = (props) => (
    <div className = 'card-container'>
         <input className = 'box'
            type='search'
            placeholder = "Enter player name"
            
        />
        <select name="Number of Players"   id="cars" onClick = {changeColor}>
        <option value="none">---</option>
        <option value="Red">Red</option>
        <option value="Blue">Blue</option>
        <option value="Orange">Orange</option>
        <option value="White">White</option>
        <option value="Purple">Purple</option>
        <option value="Yellow">Yellow</option>
        </select>

        
        

        <div id = {props.playerName + "ore" } count = "0"> 
            <h2 className = "btn" >üåë   </h2><h2 className = "btn" > 0 </h2><button className = "btn" onClick = {handleClick}  >+</button> <button className = "btn" onClick = {handleClickMinus}>-</button>
        </div>

        <div id = {props.playerName + "wheat" } count = "0"> 
            <h2 className = "btn" >üçû   </h2><h2 className = "btn" > 0 </h2><button className = "btn" onClick = {handleClick}  >+</button> <button className = "btn" onClick = {handleClickMinus}>-</button>
        </div>

        <div id = {props.playerName + "sheep" } count = "0"> 
            <h2 className = "btn" >üêë   </h2><h2 className = "btn" > 0 </h2><button className = "btn" onClick = {handleClick}  >+</button> <button className = "btn" onClick = {handleClickMinus}>-</button>
        </div>

        <div id = {props.playerName + "wood" } count = "0"> 
            <h2 className = "btn" >üå≤   </h2><h2 className = "btn" > 0 </h2><button className = "btn" onClick = {handleClick}  >+</button> <button className = "btn" onClick = {handleClickMinus}>-</button>
        </div>

        <div id = {props.playerName + "brick" } count = "0"> 
            <h2 className = "btn" >üß±   </h2><h2 className = "btn" > 0 </h2><button className = "btn" onClick = {handleClick}  >+</button> <button className = "btn" onClick = {handleClickMinus}>-</button>
        </div>
        
    </div>
)